export default {
	data() {
		return {
			alreadyLogin: false,
			isWX: false,
			userInfo: null,
			userInfoServer: null,
			phone: "",
			code: "",
			text: "发送验证码"
		}
	},
	onShow: function() {
		if (getApp().getCode()) {
			this.alreadyLogin = true
		}
		uni.onSocketMessage((res) => {
			this.getMessage(JSON.parse(res.data))
		})
		this.isWX = uni.getSystemInfoSync().platform === 'devtools'
	},
	methods: {
		getMessage: function(data) {
			let msg = data.msg
			let obj = data.obj
			if (msg == "loginSuccess") {
				getApp().setCode(obj.loginCode)
				this.alreadyLogin = true
				uni.showToast({
					icon: 'success',
					title: '登陆成功'
				})
			} else if (msg == "loginError") {
				uni.showToast({
					icon: 'none',
					title: '登陆失败'
				})
			} else if (msg == "bindPhone") {
				getApp().globalData.openid = obj.openid
				this.isWX = false
				uni.showToast({
					icon: 'none',
					title: '请绑定手机'
				})
			} else if (msg == "bindPhoneSuccess") {
				getApp().sendSocket("login", {"code": getApp().globalData.openid})
				uni.showToast({
					icon: 'success',
					title: '绑定成功'
				})
			} else if (msg == "vcode") {
				this.code = obj.vcode
			}
		},
		barTo: function(e) {
			let name = e.currentTarget.dataset.name
			if (name == "scan") {
				uni.navigateTo({
					url: "/pages/" + name + "/" + name
				})
			} else {
				uni.redirectTo({
					url: "/pages/" + name + "/" + name
				})
			}
		},
		login: function () {
			let phone = this.phone
			let code = this.code
			getApp().sendSocket("login", {
				"code": phone,
				"vcode": code
			})
		},
		setPhone: function(e) {
			this.phone = e.detail.value
		},
		setCode: function(e) {
			this.code = e.detail.value
		},
		sendVcode: function(e) {
			if (this.text == "发送验证码") {
				getApp().sendSocket("sendVcode", {phone: this.phone})
				var s = 60
				this.text = `重新发送(${s})`
				var reSendTip = setInterval(() => {
					s = s - 1
					this.text = `重新发送(${s})`
					if (s <= 0) {
						clearInterval(reSendTip)
						this.text = "发送验证码"
					}
				}, 1000)
			}
		}
	},
	oauth(value) {
		uni.login({
			provider: value,
			success: (res) => {
				uni.getUserInfo({
					provider: value,
					success: (infoRes) => {
						let code = infoRes.code
						getApp().sendSocket("login", {"code": code})
					},
					fail() {
						uni.showToast({
							icon: 'none',
							title: '登陆失败'
						});
					}
				});
			},
			fail: (err) => {
				console.error('授权登录失败：' + JSON.stringify(err));
			}
		});
	},
	getUserInfo({
		detail
	}) {
		if (detail.userInfo) {
			this.userInfo = detail.userInfo
			getApp().globalData.userInfo = detail.userInfo
		} else {
			uni.showToast({
				icon: 'none',
				title: '登陆失败'
			});
		}
	},
}
